@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&family=Roboto+Mono&display=swap);
body {
	margin: 0;
	font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgb(26, 26, 26);
	color: white;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
	outline: none;
	text-decoration: none;
	padding: 2px 1px 0;
}

a:link {
	color: #b47500;
}

a:visited {
	color: #eca201;
}

a:focus {
	border-bottom: 1px solid;
}

a:hover {
	border-bottom: 1px solid;
}
:root {
	--bg-box: rgb(70, 70, 70);
}

* {
	box-sizing: border-box;
}

.App {
	text-align: center;
}

.title {
	font-weight: 700;
}

main {
	display: flex;
	flex-direction: row;
}

.monitor {
	margin-left: 2vw;
}

label {
	margin-right: .4em;
}

.buttons {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 1rem;
	grid-gap: .5rem;
	gap: .5rem;
}

button {
    display: inline-block;
    border: none;
	border-radius: .5rem;
    padding: .2rem;
    margin: 0;
    text-decoration: none;
    background: #424242;
    color: #ffffff;
    font-family: sans-serif;
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    transition: background 250ms ease-in-out, 
                transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
}

button:hover,
button:focus {
    background: #0053ba;
}

button:focus {
    outline: 1px solid #fff;
    outline-offset: -4px;
}

button:active {
    transform: scale(0.99);
}

.monitor {
	width: 42vw;
}

.memory {
	background-color: rgb(70, 70, 70);
	background-color: var(--bg-box);
	border-radius: 15px;
}

.memory-row {
	display: flex;
	flex-direction: row;
}

.memory-item {
	min-width: 5ch;
	height: -webkit-max-content;
	height: max-content;
	font-family: 'Roboto Mono', 'Courier New', Courier, monospace;
	border: 1px solid rgb(70, 70, 70);
	border: 1px solid var(--bg-box);
}

.mem-reg-container {
	display: flex;
	flex-direction: row;
	grid-gap: 2rem;
	gap: 2rem;
}

.registers {
	background-color: rgb(70, 70, 70);
	border-radius: 15px;
	padding-left: 1.5rem;
	padding-right: 1.5rem;
	font-family: 'Roboto Mono', 'Courier New', Courier, monospace;
}

.register {
	text-align: start;
	border: 1px solid rgb(70, 70, 70);
	border: 1px solid var(--bg-box);
}

.output-box {
	background-color: rgb(70, 70, 70);
	border-radius: 15px;
	min-height: 13vh;
	width: 100%;
}

.output-box > h2 {
	margin-bottom: 1px;
}

.output-box > p {
	margin-top: 1px;
}

.forms {
	display: flex;
	flex-direction: row;
	grid-gap: 1rem;
	gap: 1rem;
	justify-content: center;
}

.input-form {
	margin-bottom: .2rem;
}

label {
	display: inline-block;
	text-align: right;
}

.error-output {
	color: rgb(252, 109, 93);
}

.green-border {
	border: 1px solid green;
}

.red-border {
	border: 1px solid rgb(224, 35, 35);
}

.yellow-border {
	border: 1px solid rgb(236, 177, 12);
}
.description > section {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.description p {
	max-width: 90ch;
	text-align: center;
}
